
.wrapLink{
    margin-top: 200px;
    display: flex;
    justify-content: space-around;
    align-items: center;
width: 100%;
    height: 719px;
    background-image: url("../../images/artsMain.jpg");
    background-size: 100% 100%;
    margin-top: 0;
transition: 0.5s ease;
}
.wrapLinkTwo{
    margin-top: 200px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 719px;
    background-image: url("../../images/clothesMain.jpg");
    background-size: 100% 100%;
    margin-top: 0;
    transition: 0.5s ease;

}
.active{
    color: gold!important;
    border-bottom: 1px solid black!important;
    transition: 0.5s ease;
}




.wrap__item{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 50%;
    padding: 30px;
    margin-top: 0;
background-color: rgb(255, 255, 255,0.40);
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
}
.link__item{
    font-size: 30px;
    color: black;
    text-decoration: none;
    transition: 0.5s ease;
    border-bottom: none;
}
.link__item:hover{
    color: gold;
}
.works{
    display: flex;
    justify-content: center;
    flex-direction: column;
height: 500px;
    width: 100vw;
    height: 100%;
align-items: center;
}
.works__picture{
    width: 100%;
    display: flex;
    justify-content: center;
align-items: center;
}
.imageSize{
    width: 290px;
    height: 400px;

}
.imageSize img{
    width: 290px;
    height: 400px;
}
.wrapWorkComponents{

}
.wrapWorkComponents h2{
    text-align: center;
}
.itemWrap{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.itemWork{
    margin-right: 20px;
    margin-bottom: 20px;
}
.sliderWrap{
    width: 30vw;
    height: auto;

}
