.slick-prev:before, .slick-next:before{
    background-color: black !important;
    border-radius: 50%;

}
.sliderWrap img{
    width: 100%;
    height: 500px ;
}

@media only screen and (max-width: 484px) {
.imageSize{
    margin-right: 0;
    width: 100%;
}
    .imageSize img{
        width: 100%;
        height: 400px;
    }
    .wrapLink{
        height: 400px;
    }
    .wrapLinkTwo{
        height: 400px;
    }
    .wrap__item{
        justify-content: space-between;
    }
    .link__item{
        font-size: 13px;

    }
    .sliderWrap{
        width: 90vw;
        height: auto;

    }
    .slick-prev:before, .slick-next:before{
    display: none;

    }
}